* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: #d2dbdd;
}

.heading {
  padding: 30px 0;
  text-align: center;
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  font-size: 1.5rem;
  margin-bottom: 30px;
}

.cards {
  width: 100%;
  height: auto;
}

.card {
  margin-left: 12%;
  margin-top: 6%;
  margin-bottom: 2%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  width: 18%;
  border-radius: 12%;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  float: left;
}

.card:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.card_img {
  width: 100%;
  height: 435px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card_info {
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
}

.card_category {
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 600;
  display: grid;
  place-items: center;
  /* margin-left: 20px; */
}

.card_title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: sans-serif;
  text-align: center;
}

button {
  padding: 5px 8px;
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
  border-radius: 20px;
}
button:hover {
  background-color: tomato;
  color: #fff;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

a {
  text-decoration: none;
}
